* {
    margin: 0; 
    padding: 0;
    box-sizing: border-box;

}

html {
    font-family: 'Opens sans', sans-serif;
    font-size:20px;
}
div.app {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    min-height: 100vh;
    width:90%;
    margin: 0 auto;
    
    color: #222;
}

header {
    -webkit-flex-basis: 30vh;
            flex-basis: 30vh;
   
}

main {
    padding: 30px 0;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-grow:1;
            flex-grow:1;
}

aside {
    min-width: 240px;
    
}

section.page {
    -webkit-flex-grow:1;
            flex-grow:1;
    
}
footer{
    background-color: black;
    color: #fff;
    text-align: center;
    padding: 10px 0;
    text-transform: uppercase;
    letter-spacing: 3px;
}
header {
    position: relative;
    overflow: hidden;
}

header img {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    min-width: 100%;
    min-height: 100%;

}
.main ul {
    list-style: none;
}

nav.main{
    
    padding: 0 30px 30px 0;
}

nav.main a {
    display: block;
    text-decoration: none;
    color: #333;
    text-transform: uppercase;
    font-weight: bold;
    padding: 10px;
    -webkit-transition: 0.2s;
    transition: 0.2s;

}

nav.main a:hover {
    -webkit-transform: translateX(20px);
            transform: translateX(20px)
}

nav.main a.active {
    border-left: 10px solid grey;
    -webkit-transform: translateX(0);
            transform: translateX(0)
}
nav.main li:nth-child(1) a.active {
    border-left-color: green;
}
nav.main li:nth-child(2) a.active {
    border-left-color: blue;
}
nav.main li:nth-child(3) a.active {
    border-left-color: red;

}
nav.main li:nth-child(4) a.active {
    border-left-color: rgb(255, 145, 0);

}


h1 {
    margin-bottom: 20px;
}

h2 {
    margin-top:20px
}

p{
    margin: 20px 0;
}

small {
    font-size: 0.7rem;
    color: rgb(77, 77, 77);
}


.products h2{

margin-bottom: 20px;
    
}

.products ul {
    list-style: square;
    padding-left: 30px;
}

.products li {
    margin-bottom: 20px;
}

.products a {
    text-decoration: none;
    color: #333;
}

.products a:hover {
    text-decoration: underline;
}
.contact textarea {

    font-size: 16px;
    display: block;
    margin: 20px 0;
    width: 80%;
    min-height: 200px;
    line-height: 1.5;
}

.contact button {
    border: 2px solid #333;
    background-color: #fff;
    padding: 10px 12px;
    cursor:pointer;
    display: block;
    margin: 0 20% 0 auto;
}
.contact button:hover {
    color: white;
    background-color: black;
}
